<template>
  <div class="index-container">
    <base-slider class="crousel"
                 :list="imgList"
                 :height="640"
                 @sliderClick="sliderClick">
    </base-slider>
    <!--&lt;!&ndash;<base-page :total="110"></base-page>&ndash;&gt;-->
    <div class=" search-container">
      <div class="search-wrapper">
        <p class="title">从这里开始</p>
        <p class="tips">输入您需要的保障，我们会为您推荐合适的产品</p>
        <base-search-box @search="onSearch"></base-search-box>
        <div class="hot-search clearfix">
          <p class="hot-title fl">或换一个推荐关键词：</p>
          <ul class="hot-list fl clearfix">
            <li @click="goList">旅游险</li>
          </ul>
        </div>
      </div>
    </div>
    <div class="layout">
      <div class="layer layer-categogy">
        <p class="title">
          产品与服务
        </p>
        <ul class="product-category clearfix">
          <li class="yw">
            <router-link tag="div" to="/37duc/product/list?productCategoryId=2&page=1">
              <p>意外伤害</p>
            </router-link>
          </li>
          <li class="yl">
            <router-link tag="div" to="/37duc/product/list?productCategoryId=3&page=1">
              <!--<img src="../assets/images/category_yl.png" alt="">-->
              <p>医疗保险</p>
            </router-link>
          </li>
          <li class="jb">
            <router-link tag="div" to="/37duc/product/list?productCategoryId=5&page=1">
              <!--<img src="../assets/images/category_jb.png" alt="">-->
              <p>疾病保险</p>
            </router-link>
          </li>
          <li class="hl">
            <router-link tag="div" to="/37duc/product/list?productCategoryId=4&page=1">
              <!--<img src="../assets/images/category_hl.png" alt="">-->
              <p>护理保险</p>
            </router-link>
          </li>
        </ul>
      </div>
      <div class="layer layer-recommend">
        <p class="title">
          产品推荐
        </p>
        <div class="product clearfix">
          <img src="../assets/images/tj.png" alt="">
          <div class="product-wrapper">
            <div class="list clearfix">
              <div class="part-lt fl">
                <div class='pName'>{{prodcutDetail.name}}</div>
                <div class="pMsg">
                  <span class="condition-item" v-for="(item,index) in prodcutDetail.insure_condition"
                        :key="index">{{item.name}}：{{item.value}}</span>
                </div>
                <div class="marks clearfix">
                  <div class='mark fl' >
                    <span class="fl" v-if="prodcutDetail.feature1"><i></i>{{prodcutDetail.feature1}}</span>
                    <span class="fl" v-if="prodcutDetail.feature2"><i></i>{{prodcutDetail.feature2}}</span>
                    <span class="fl" v-if="prodcutDetail.feature3"><i></i>{{prodcutDetail.feature3}}</span>
                  </div>
                </div>
                <div class="characteristics">
                  <div class="character clearfix"
                       v-for="(item,index) in parameters" :key="index">
                    <span class="fl">{{item.name}}</span>
                    <span class="fr">{{item.limit}}</span>
                  </div>
                </div>
                <div class="logo"><img :src="baseSrc+prodcutDetail.brandLogo" alt="公司logo"></div>
              </div>
              <div class="part-rt">
                <div class="ctx">
                  <div class="price"><span class="p">{{prodcutDetail.price}}</span><span class="b">元起</span>
                  </div>
                  <div class="toDetail" @click="goDetail(1)">查看详情</div>
                  <slot></slot>
                </div>
              </div>
            </div>

          </div>
        </div>

      </div>
      <div class="layer layer-company">
        <p class="title">
          合作品牌
        </p>
        <ul class="coper-company clearfix">
          <li><img src="../assets/images/al.png" alt=""></li>
          <li><img src="../assets/images/tpy.png" alt=""></li>
          <li><img src="../assets/images/pa.png" alt=""></li>
          <li><img src="../assets/images/rm.png" alt=""></li>
          <li><img src="../assets/images/hz.png" alt=""></li>
        </ul>
      </div>
    </div>
    <!--<v-distpicker :placeholders="placeholders"></v-distpicker>-->
  </div>
</template>

<script>
import baseSlider from '@/components/BaseSlider.vue'
import baseSearchBox from '@/components/BaseSearchBox.vue'
import { getLocalStorage } from '@/assets/js/localStorage'
// import VDistpicker from 'v-distpicker'
export default {
  name: 'home',
  data () {
    return {
      baseSrc: '',
      memberId: '',
      parameters: [],
      prodcutDetail: {},
      imgList: [
        { src: require('../assets/images/c.jpg') }
      ]
    }
  },
  mounted () {
    this.baseSrc = `${process.env.VUE_APP_RESOURCE_URL}`
    if (getLocalStorage('userInfo')) {
      this.memberId = getLocalStorage('userInfo').id
    }
    this.getDetail()
  },
  methods: {
    onSearch (query) {
      if (query) {
        this.$router.push({
          path: '/37duc/product/list'
        })
      }
    },
    goList () {
      this.$router.push({
        path: '/37duc/product/list'
      })
    },
    onMatchSearch (newQuery) {
    },
    sliderClick (i) {
      console.log(i)
    },
    goDetail (id) {
      this.$router.push({
        path: `/37duc/product/detail/${id}`,
        query: {
          productId: 1
        }
      })
    },
    getDetail () {
      let postData = {
        id: 1,
        memberId: this.memberId
      }
      this.$api.product.productDetail(postData).then(res => {
        this.prodcutDetail = res.data.product
        this.parameters = res.data.product.parameter_values[0].entries
      })
    }
  },
  components: {
    baseSlider,
    baseSearchBox
    // VDistpicker
  }
}
</script>
